window.addEventListener('load', function () {

    // mobile menu
    $('.menu-btn').on('click', function() {
      
        $(this).toggleClass('menu-btn_active');
        
        $('.nav_menu').addClass('menu_active');

        if(!$(this).hasClass('menu-btn_active')){
            $('.nav_menu').removeClass('menu_active');
        }
    });

    // mobile menu end

    // sliders
    $('.fade').slick({
        dots: true,
        infinite: true,
        speed: 700,
        fade: true,
        cssEase: 'linear',
        autoplay: true,
        autoplaySpeed: 3000,
        mobileFirst: true
      });

      $('.multiple-items').slick({
        infinite: true,
        slidesToShow: 2,
        slidesToScroll: 2,
        dots: true,
        arrows: false,
        autoplay: true,
        autoplaySpeed: 3000,
        mobileFirst: true,
        rows: 2,
        });

    // sliders end

    // up

    var $btn = $('.btn-up');
    var h = 300;
    var btnShowed = false;
    
    function onScrollEnd(){
        var scrollTop = $(this).scrollTop();
        
        if(!btnShowed && scrollTop >= h){
            $btn.fadeIn(20);
            btnShowed = true;
        }
        else if(btnShowed && scrollTop < h){
            $btn.fadeOut(20);
            btnShowed = false;
        }
    }
    
    var scrollTimer;
    
    $(document).on('scroll', function(){
        var document = this;
        
        if(scrollTimer !== undefined){
            clearInterval(scrollTimer);
        }
        
        scrollTimer = setTimeout(function(){
            onScrollEnd.call(document);
        }, 200);
    });

    $btn.on('click', function(){
        $('html, body').animate({
            scrollTop: 0
        }, 700);
    });

    
    onScrollEnd.call(document);

    // up end

    $('.log_open').on('click', function (e) {
        e.preventDefault();
        $('.mod1').css('display', 'block')
    });
    $('.reg_open').on('click', function (e) {
        e.preventDefault();
        $('.mod2').css('display', 'block')
    });

    $('.close_modal').on('click', function () {
        $('.modal').css('display', 'none')
    });
});