window.addEventListener('load', function () {
    var tabBtn = document.querySelectorAll('.tabBtn');
    var tabDash = document.querySelectorAll('.tabDash');

    function open(a, b, c, d, e){
        a.style.display = 'block';
        b.style.display = 'none';
        c.style.display = 'none';
        d.style.display = 'none';
        e.style.display = 'none';
    }

    var activeBgColor = '#3b7dc4';
    var passiveBgColor = '#2262a5';

    function setBg(activeBtn){
        activeBtn.style.backgroundColor = activeBgColor;
    }

    function passiveBg(pas1, pas2, pas3, pas4) {
        pas1.style.backgroundColor = passiveBgColor;
        pas2.style.backgroundColor = passiveBgColor;
        pas3.style.backgroundColor = passiveBgColor;
        pas4.style.backgroundColor = passiveBgColor;
    }

    tabBtn[2].style.backgroundColor = activeBgColor;
    tabDash[2].style.display = 'block';

    tabBtn[0].addEventListener('click', function () {
        open(tabDash[0], tabDash[1], tabDash[2], tabDash[3], tabDash[4]);
        setBg(this);
        passiveBg(tabBtn[1], tabBtn[2], tabBtn[3], tabBtn[4]);
    });
    tabBtn[1].addEventListener('click', function () {
        open(tabDash[1], tabDash[0], tabDash[2], tabDash[3], tabDash[4]);
        setBg(this);
        passiveBg(tabBtn[2], tabBtn[3], tabBtn[4], tabBtn[0]);
    });
    tabBtn[2].addEventListener('click', function () {
        open(tabDash[2], tabDash[1], tabDash[0], tabDash[3], tabDash[4]);
        setBg(this);
        passiveBg(tabBtn[1], tabBtn[3], tabBtn[4], tabBtn[0]);
    });
    tabBtn[3].addEventListener('click', function () {
        open(tabDash[3], tabDash[1], tabDash[2], tabDash[0], tabDash[4]);
        setBg(this);
        passiveBg(tabBtn[2], tabBtn[1], tabBtn[4], tabBtn[0]);
    });
    tabBtn[4].addEventListener('click', function () {
        open(tabDash[4], tabDash[1], tabDash[2], tabDash[0], tabDash[3]);
        setBg(this);
        passiveBg(tabBtn[1], tabBtn[3], tabBtn[2], tabBtn[0]);
    });
});